import React from 'react';
import Symptom from "./Symptom";
import { useState, useEffect } from 'react';
import Papa from 'papaparse';
import Data from '../data/mainRearranged.csv';

function SymptomBoard(props) {
    const [csvDataArray, setCsvDataArray] = useState([]);

    useEffect(() => {
        fetch(Data)
          .then(response => response.text())
          .then(csvText => {
            Papa.parse(csvText, {
              complete: function(results) {
                setCsvDataArray(results.data);
              }
            });
          })
          .catch(error => console.error('Error fetching CSV:', error));
      }, []);
    let symptomName;
    return (
        <div className="w-full h-96 overflow-x-scroll flex flex-wrap outline rounded">
            {csvDataArray.length > 0 && csvDataArray[0].map((entry, i) => {
                if (i > 1) {
                    symptomName = csvDataArray[0][i].slice(14);
                    //add a prop here so that i is returned
                    return <Symptom name={symptomName} index={i} sub={props.sub} />;
                }
            })}
        </div>
    );
}

export default SymptomBoard;