import React from 'react';
import { useGlobalState } from '../context/GlobalStateContext';

function Symptom(props) {
    const { globalState, updateGlobalState } = useGlobalState();

    const handleClick = () => {
        const exists = globalState.some(item => item[0] === props.name);
        
        if (!exists) {
            const newPair = [props.name, props.index];
            updateGlobalState(prevState => [...prevState, newPair]);
        }
    }

    return (
        <div className=" pt-1">
            <button className="text-white bg-gray-800 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-full text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:hover:bg-gray-700 dark:focus:ring-gray-700 dark:border-gray-700" onClick={handleClick} disabled={props.sub}>{props.name}</button>
        </div>
    );
}

export default Symptom;
