import React from 'react';
import { useGlobalState } from '../context/GlobalStateContext';
import { useState, useEffect } from 'react';
import Papa from 'papaparse';
import Data from '../data/mainRearranged.csv';
import Disease from './Disease';

function Diagnosis(props) {
    //currently the global state is all the symptoms the user selected
    //how do i want to transfer that over to a diagnosis?
    //maybe get a useState here for the big disease array

    const { globalState } = useGlobalState();
    const [diseases, setDiseases] = useState([]);
    const [possibleDiseases, setPossibleDiseases] = useState([]);

    useEffect(() => {
        fetch(Data)
          .then(response => response.text())
          .then(csvText => {
            Papa.parse(csvText, {
              complete: function(results) {
                setDiseases(results.data);
              }
            });
          })
          .catch(error => console.error('Error fetching CSV:', error));
      }, []);

      useEffect(() => {
        // Update possibleDiseases based on selected symptoms
        let updatedPossibleDiseases = [...diseases];
        globalState.length === 0 && setPossibleDiseases([]);
        for (let i = 0; i < globalState.length; i++) {
            const symptomIndex = globalState[i][1];
            //for every selected symptom
            for (let j = 0; j < updatedPossibleDiseases.length; j++) {
                //for every possible disease
                if (updatedPossibleDiseases[j][symptomIndex] !== 1) {
                    updatedPossibleDiseases.splice(j,1);
                }
            }
        }
        setPossibleDiseases(updatedPossibleDiseases);
    }, [globalState]);

    let name;
    let totalFrequency = 0;

    for (let i = 0; i < possibleDiseases.length; i++) {
        totalFrequency += parseInt(possibleDiseases[i][1]);
    }

    if (!props.sub) {
        return null;
    }

    if (props.sub && globalState.length == 0) {
        return <h1 className="font-medium text-gray-300">No symptoms selected</h1>
    }

    if (props.sub && possibleDiseases.length == 0) {
        return <h1 className="font-medium text-gray-300">A diagnosis for this set of symptoms cannot be determined</h1>
    }

    return (
        <div>
            <h1 className="grid place-items-center font-medium text-gray-300 pb-2">Possible Diagnoses</h1>
            <div className="w-screen max-h-96 overflow-y-auto flex items-center">
                {possibleDiseases.map(item => (
                    name = item[0].slice(14),
                    <Disease name={name} freq={((item[1] / totalFrequency) * 100).toFixed(2)}/>
                ))}
            </div>
        </div>
    );
}

export default Diagnosis;