import React from 'react';
import { useGlobalState } from '../context/GlobalStateContext';

function SelectedSymptoms(props) {
    const { globalState, updateGlobalState, clearGlobalState } = useGlobalState();

    const handleClick = (itemToRemove) => {
        const updatedState = globalState.filter(item => item[0] !== itemToRemove[0]);
        updateGlobalState(updatedState);
    }

    const handleClear = () => {
        clearGlobalState();
    }
    return (
        <div className="grid place-items-center">
            <div className="p-4">
                <h1 className="grid place-items-center font-medium pb-2 text-gray-300">Currently selected symptoms (click blue symptom to remove)</h1>
                {globalState.length > 0 && globalState.map(item => (
                    <button className="text-white bg-blue-700 hover:bg-red-800 focus:outline-none focus:ring-4 focus:ring-blue-300 font-medium rounded-full text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:bg-blue-600 dark:hover:bg-red-700 dark:focus:ring-blue-800" key={item[0]} onClick={() => handleClick(item)} disabled={props.sub}>{item[0]}</button>
                ))}
            </div>
            {!props.sub && <button className="text-white bg-red-700 hover:bg-red-800 focus:outline-none focus:ring-4 focus:ring-red-300 font-medium rounded-full text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900"onClick={handleClear} disabled={props.sub}>Clear symptoms</button>}
        </div>
    );
}

export default SelectedSymptoms;