import React from 'react';
import Tooltip from '@mui/material/Tooltip';

function Disease(props) {
    const googleSearchUrl = `https://www.google.com/search?q=${encodeURIComponent(props.name)}`;

    const handleClick = () => {
        window.open(googleSearchUrl);
    }

    const description = `Based purely on the given symptoms and a sample population, there is a ${props.freq}% chance that ${props.name} is your ailment`;

    return (
        <div className="w-full flex items-center font-medium bg-gray-500">
            <button className="flex items-center focus:outline-none text-white bg-purple-700 hover:bg-purple-800 focus:ring-4 focus:ring-purple-300 font-medium rounded-l-lg text-sm px-5 py-2.5 mb-2 dark:bg-purple-600 dark:hover:bg-purple-700 dark:focus:ring-purple-900 h-20" onClick={handleClick}>{props.name}</button>
            {/* <h1 className="text-purple-700 hover:text-white border border-purple-700 hover:bg-purple-800 focus:ring-4 focus:outline-none focus:ring-purple-300 font-medium rounded-r-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-purple-400 dark:text-purple-400 dark:hover:text-white dark:hover:bg-purple-500 dark:focus:ring-purple-900">{props.freq}%</h1> */}
            <Tooltip title={description} placement="top" arrow>
                <h1 className="flex items-center text-purple-700 hover:text-white border border-purple-700 hover:bg-purple-800 focus:ring-4 focus:outline-none focus:ring-purple-300 font-medium rounded-r-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-purple-400 dark:text-purple-400 dark:hover:text-white dark:hover:bg-purple-500 dark:focus:ring-purple-900 h-20">{props.freq}%</h1>
            </Tooltip>
        </div>
    );
}

export default Disease;