import './App.css';
import React from 'react';
import SymptomBoard from './components/SymptomBoard';
import { GlobalStateProvider } from './context/GlobalStateContext';
import SelectedSymptoms from './components/SelectedSymptoms';
import { useState } from 'react';
import Diagnosis from './components/Diagnosis';

function App() {
  const [submitted, setSubmitted] = useState(false);

  const handleClick = () => {
    setSubmitted(!submitted);
  }

  return (
    <GlobalStateProvider>
      <div className="h-full w-screen grid place-items-center bg-gray-500">
        <SelectedSymptoms sub={submitted} />
        <h1 className="font-medium text-gray-300 pb-2">Choose Symptoms</h1>
        <SymptomBoard sub={submitted} />
        <div className="pt-2">
          {!submitted ? <button onClick={handleClick} className="text-white bg-green-700 hover:bg-green-800 focus:outline-none focus:ring-4 focus:ring-green-300 font-medium rounded-full text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:bg-green-600 dark:hover:bg-green-700 dark:focus:ring-green-800">Submit</button>
          : <button onClick={handleClick} className="text-white bg-red-700 hover:bg-red-800 focus:outline-none focus:ring-4 focus:ring-red-300 font-medium rounded-full text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900">Back</button>}
        </div>
        <Diagnosis sub={submitted} />
      </div>
    </GlobalStateProvider>
  );
}

export default App;
